.itm_newyear {
  padding: 28px 0;
}

.itm_newyear p,
.itm_newyear h1,
.itm_newyear h2,
.itm_newyear h3,
.itm_newyear h4 {
  margin: 0;
}

@media screen and (min-width: 768px) {
  .itm_newyear {
    padding: 55px 0;
  }
}

@media screen and (min-width: 1280px) {
  .itm_newyear__expect .expect_wrapper {
    max-width: 1440px;
  }
}

.itm_newyear__expect .expect_question {
  font-size: 24px;
  font-weight: 700;
  text-align: center;
  margin-bottom: 44px;
}

@media screen and (min-width: 768px) {
  .itm_newyear__expect .expect_question {
    margin-bottom: 50px;
    text-align: start;
    font-size: 40px;
  }
}

.itm_newyear__expect .expect_days-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 32px;
  margin-bottom: 51px;
  padding-left: 110px;
  position: relative;
}

.itm_newyear__expect .expect_days-item {
  position: relative;
}

.itm_newyear__expect .expect_days-item > h3 {
  margin-bottom: 4px;
  font-size: 20px;
  text-transform: uppercase;
  line-height: 1.2;
  font-weight: 700;
}

@media screen and (min-width: 1280px) {
  .itm_newyear__expect .expect_days-item > h3 {
    font-size: 32px;
    line-height: 1.22;
  }
}

.itm_newyear__expect .expect_days-item > p {
  font-size: 16px;
  line-height: 1.19;
}

@media screen and (min-width: 1280px) {
  .itm_newyear__expect .expect_days-item > p {
    font-size: 24px;
    line-height: 1.21;
  }
}

.itm_newyear__expect .expect_days-item :nth-child(1)::before {
  position: absolute;
  content: "";
}

.itm_newyear__expect .expect_suggestion {
  font-size: 24px;
  margin-bottom: 26px;
  font-weight: 700;
  text-align: center;
  display: block;
}

@media screen and (min-width: 1280px) {
  .itm_newyear__expect .expect_suggestion {
    font-size: 48px;
    margin-bottom: 22px;
  }
}

.itm_newyear__expect .expect_registration-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 296px;
  height: 72px;
  font-weight: 700;
  font-size: 20px;
  text-transform: uppercase;
  background-color: #19A1CC;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}